.operator-index {
  &__title {
    font-family: 'pn-bold';
    color: #344178;
    font-size: 18px;
    margin-bottom: 30px;
    text-align: center;
  }

  &__content {
    margin-top: 70px;
    background-color: #fff;
    padding: 40px;
    border-radius: 10px;

    .pic {
      display: inline-block;
      width: 249px;
      height: 388px;
      margin-left: 160px;
      margin-right: 130px;
      background: url('../../images/bgs/operator.png') no-repeat;
    }
  }
  
  .inputs-wrapper {
    display: inline-block;
    width: 365px;
    vertical-align: top;
    padding-top: 10px;

    .button {
      width: 210px;
      height: 40px;
      margin: 0 auto;
      display: block;
    }
  }

  .block-alert {
    font-family: 'pn-regular';
    min-height: 75px;
    text-align: center;
    color: #fff;
    border-radius: 5px;
    padding: 28px 20px;
    margin-bottom: 10px;
    background-color: #ea7711;
  }
}