.table.clubs {
  th:first-child,
  td:first-child {
    padding-left: 30px;
    width: 220px;
    max-width: 220px;
  }
  th:nth-child(2),
  td:nth-child(2) {
    width: 300px;
    max-width: 300px;
  }
  th:nth-child(3),
  td:nth-child(3) {
    width: 280px;
    max-width: 280px;
  }
  th:nth-child(4),
  td:nth-child(4) {
    width: 220px;
    max-width: 220px;
  }
}

.table.clubsall {
  th:first-child,
  td:first-child {
    padding-left: 30px;
    width: 145px;
    max-width: 145px;
  }
  th:nth-child(2),
  td:nth-child(2) {
    width: 240px;
    max-width: 240px;
  }
  th:nth-child(3),
  td:nth-child(3) {
    width: 170px;
    max-width: 170px;
  }
  th:nth-child(4),
  td:nth-child(4) {
    width: 200px;
    max-width: 200px;
  }
  th:nth-child(4),
  td:nth-child(4) {
    width: 240px;
    max-width: 240px;
  }
}